*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: 'Montserrat', sans-serif;
}

button,
input {
  border: none;
  background: transparent;
  font-family: inherit;

  &:focus {
    outline: none;
  }
}

button:hover {
  cursor: pointer;
}